

















































import { Component, Prop, Vue } from 'vue-property-decorator';
import MainPageHeader from '../components/MainPageHeader.vue';
import BigButton from '../components/input/BigButton.vue';
import SmallButton from '../components/input/SmallButton.vue';
import AuthButton from '../components/input/AuthButton.vue';
import Tabber from '../components/Tabber.vue';
import DefaultInputField from '../components/input/DefaultInputField.vue';
import PopupBackground from '../components/PopupBackground.vue';

@Component({
    name: 'LoginPage',
    components: {
        MainPageHeader,
        BigButton,
        SmallButton,
        AuthButton,
        DefaultInputField,
        Tabber,
        PopupBackground,
    },
})
export default class LoginPage extends Vue {
    email: string = '';
    password: string = '';
    rememberMe: boolean = false;
    activeTab: number = 0;
    textExpanded: boolean = false;

    tabSelected(tab: number): void {
        this.activeTab = tab;
    }
}
