<template>
    <div class="Tabber">
        <div class="tab-header"
            v-for="(tab, index) in tabs"
            :key="index"
            @click="tabClicked(index)"
            :class="{
                active: index == activeTab
            }">
            {{tab}}
        </div>
        <div class="selection-box" :style="{
            ['--tab-length']: `${100/tabs.length}%`,
            ['--tab-offset']: `${100/tabs.length * activeTab}%`,
        }"></div>
    </div>
</template>

<script>
    export default {
        name: 'Tabber',
        components: {
        },
        props: {
            tabs: {
                type: Array,
                required: true,
            },
            activeTab: {
                type: Number,
                default: 0,
            },
        },
        computed: {
            computedClass() {
                return {
                    'opened': this.opened,
                };
            },
        },
        methods: {
            tabClicked(index) {
                this.$emit('tabselected', index);
            },
        },
    };
</script>

<style scoped lang="scss">
    @import "@/assets/scss/_variables.scss";

    .Tabber {
        position: relative;
        display: flex;
        align-items: flex-start;
        margin: 20px 0;

        .tab-header {
            box-sizing: content-box;
            padding: 10px 20px;
            flex: 1 1;
            color: #eee;
            border-bottom: 1px solid #aaa;
            cursor: pointer;
            transition: color 0.1s ease-out 0.1s, border 0.1s ease-out;

            &.active {
                color: $primary;
                border-color: $primary;
            }
        }

        .selection-box {
            position: absolute;
            bottom: 0;
            border-top: 3px solid $primary;
            width: var(--tab-length, 100px);
            left: var(--tab-offset, 0px);

            transition: 0.2s ease-out left;
        }
    }
</style>
